<template>
  <section class="login">
    <h2>login</h2>
    <form @submit.prevent="login">
      <input type="email" placeholder="Enter Email" minlength="3" maxlength="20" v-model="user.email" required autofocus>
      <input type="password" placeholder="Enter Password" minlength="2" maxlength="15" v-model="user.pass" required>
      <button>Login</button>
    </form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      user: {email: '', pass: ''},
      loggedInUser: null
    };
  },
  methods:{
    login(){
      this.$store.dispatch({type: 'login', user:this.user})
      .then(()=>{
        this.user = {email: '', pass: ''}
      })
    }
  }
};
</script>


<style lang="scss" scoped>
input,button{
    background-color: white;
    padding: 5px;
    border: black 1px solid;
}
</style>
