<template>
  <section class="trip-edit">
      <h2>Edit</h2>
    <form @submit.prevent="save">
      <input type="text" v-model="trip.title" placeholder="Enter title" />
      <input type="text" v-model="trip.desc" placeholder="Enter description" />
      <input type="text" />
      <button>Save</button>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      trip: { title: "", desc: "" }
    };
  },
  created() {
    const tripId = this.$route.params.id;
    if (tripId) {
      this.$store
        .dispatch({ type: "getById", tripId })
        .then(trip => (this.trip = trip));
    }
  },
  methods: {
    save() {
      this.$store
        .dispatch({ type: "save", trip: this.trip })
        .then(() => console.log("Trip Saved"));
    }
  }
};
</script>