<template>
  <section class="signup">
    <h2>signup</h2>
    <form @submit.prevent="signup">
      <input type="email" placeholder="Email" v-model="newUser.email" />
      <input type="password" placeholder="Password" v-model="newUser.pass" />
      <input type="text" placeholder="First Name" v-model="newUser.firstName" />
      <input type="text" placeholder="Last Name" v-model="newUser.lastName" />
      <input type="text" placeholder="Year Of Birth" v-model="newUser.yearOfBirth" />
      <textarea cols="30" rows="10" v-model="newUser.aboutMe"></textarea>
      <button>Signup</button>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      newUser: {
        email: "",
        pass: "",
        firstName: "",
        lastName: "",
        avaterImgUrl: "",
        aboutMe: "",
        yearOfBirth: "",
        interests: "",
        socialMedia: ""
      }
    };
  },
  methods: {
    signup() {
      this.$store.dispatch({ type: "signup", userCredential: this.newUser })
      .then(()=>{})
    }
  }
};
</script>


<style lang="scss" scoped>
input,
textarea,
button {
  background-color: white;
  padding: 5px;
  border: black 1px solid;
}
</style>
