<template>
  <section>
    <h1>home</h1>
    <TripFilter />
    <TripList :trips="getTrips" />
  </section>
</template>

<script>
import TripList from "../components/TripList";
import TripFilter from "../components/TripFilter";

export default {
  data() {
    return {};
  },
  created() {
    // this.loadOwnerTrips()
    this.loadTrips();
  },
  computed: {
    getTrips() {
      return this.$store.getters.getTrips;
    }
  },
  methods: {
    // loadOwnerTrips(){

    // }
    loadTrips() {
      this.$store.dispatch("loadTrips");
    }
  },
  components: {
    TripList,
    TripFilter
  }
};
</script>
