import TripService from '../Services/TripService.js'



export default {
    state: {
        trips: [],
        ownerTrips: []
    },
    mutations: {
        setTrips(state, { trips }) {
            state.trips = trips;
        },
        remove(state, { id }) {
            const idx = state.trips.findIndex(currTrip => currTrip._id === id)
            state.trips.splice(idx, 1)
        },
        update(state, { trip }) {

            var idx = state.trips.findIndex(cuurTrip => cuurTrip._id === trip._id)
            state.trips.splice(idx, 1, trip)
        },
        save(state, { trip }) {
            state.trips.push(trip)
        }
    },
    getters: {
        getTrips(state) {
            return state.trips;
        }
    },
    actions: {
        loadTrips(context) {
            TripService.query()
                .then(trips => {
                    context.commit({ type: 'setTrips', trips })
                })
        },
        getById(context, { tripId }) {
            return TripService.getById(tripId)
                .then(trip => {
                    return trip;
                })
        },
        remove(context, { id }) {
            TripService.remove(id)
            then(() => {
                context.commit({ type: 'remove', id })
            })
        },
        save(context, { trip }) {
            if (trip._id) {
                TripService.save(trip)
                    .then(trip => {
                        context.commit({ type: 'update', trip })
                    })
            } else {
                TripService.save(trip)
                    .then(trip => {
                        context.commit({ type: 'save', trip })
                    })
            }
        },
    }
}