import HttpService from './HttpService'

export default {
    query,
    getById,
    remove,
    save
}

function query() {
    
    return HttpService.get(_getUrl())
}

function getById(id){
    return HttpService.get(_getUrl(id))
}

function remove(id){
    return HttpService.delete(_getUrl(id))
}

function save(trip){
    if(trip._id){
        return HttpService.put(_getUrl(trip._id), trip)
    }else{
        return HttpService.post(_getUrl(), trip)
    }
}

function _getUrl(id=''){
    return `trip/${id}`
}





























