<template>
  <section v-if="trip" class="trip-details">
    <h2>details</h2>
    <router-link :to="'/TripEdit/'+trip._id">Edit</router-link>
    <h2>prev</h2>
    <h3>{{trip._id}}</h3>
    <h3>{{trip.title}}</h3>
    <h3>Owner: {{trip.owner.fullName}}</h3>
    <img :src="trip.owner.avaterImgUrl" />
    <h3>{{trip.destination}}</h3>
    <h3>{{trip.participantCap}}</h3>
    <button @click="remove(trip._id)">Remove Trip</button>
  </section>
</template>


<script>
export default {
  data() {
    return {
      trip: null
    };
  },
  created() {
    const tripId = this.$route.params.id;
    this.$store
      .dispatch({ type: "getById", tripId })
      .then(trip => (this.trip = trip));
  },
  methods: {
    remove(id) {
      this.$store.dispatch({ type: "remove", id });
    }
  }
};
</script>
